"use client"

import { useEffect, useState } from "react"
import { useParams } from "next/navigation"
import { Button } from "@/components/ui/button"
import Header from "@/components/header"
import Footer from "@/components/footer"
import { ChevronLeft } from "lucide-react"
import Link from "next/link"

const API_BASE = process.env.NEXT_PUBLIC_API_URL || "http://localhost:5000"

export default function AssessmentFormPage() {
  const params = useParams()
  const assessmentId = params.id as string
  const [assessment, setAssessment] = useState<any>(null)
  const [answers, setAnswers] = useState<Record<number, string>>({})
  const [loading, setLoading] = useState(true)

  // âœ… Load assessment & questions
  useEffect(() => {
    const fetchAssessment = async () => {
      setLoading(true)
      try {
        const res = await fetch(
          `${API_BASE}/api/assessment/476cf119-36de-4681-b184-d81e73f5cb5c?domain_id=${assessmentId}`
        )
        const data = await res.json()

        setAssessment({
          id: data.entity_assessment_id,
          title: data.title || "",
          totalQuestions: data.totalQuestions,
          questions: data.questions.map((q: any) => ({
            id: q.id,
            text: q.text,
            hasUpload: q.requires_document,
            is_ans: q.is_ans, // ðŸ‘ˆ from DB
          })),
        })
      } catch (err) {
        console.error("Fetch assessment error:", err)
      } finally {
        setLoading(false)
      }
    }
    fetchAssessment()
  }, [assessmentId])

  // âœ… Get default_answer from backend
  const handleGetIDMCAnswer = async (questionId: number) => {
    try {
      const res = await fetch(`${API_BASE}/api/questions/${questionId}/idmc-answer`)
      const data = await res.json()
      setAnswers((prev) => ({
        ...prev,
        [questionId]: data.answer || "Answer not found",
      }))
    } catch (err) {
      console.error("Error fetching IDMC answer:", err)
      setAnswers((prev) => ({
        ...prev,
        [questionId]: "Error fetching answer",
      }))
    }
  }

  // âœ… Handle manual yes/no answer
  const handleAnswerChange = (questionId: number, answer: string) => {
    setAnswers((prev) => ({
      ...prev,
      [questionId]: answer,
    }))
  }

  if (loading) return <div>Loading...</div>
  if (!assessment) return <div>No data</div>

  return (
    <main className="min-h-screen bg-background">
      <Header />

      <section className="py-8">
        <div className="container mx-auto px-4">
          <div className="flex items-center mb-6">
            <Link href="/assessment">
              <Button variant="ghost" size="icon">
                <ChevronLeft className="w-5 h-5" />
              </Button>
            </Link>
            <h1 className="text-2xl font-bold ml-2">{assessment.title}</h1>
          </div>

          <div className="bg-white rounded-xl border border-gray-200 p-6 space-y-6">
            {assessment.questions.map((question: any, index: number) => (
              <div key={question.id} className="border-b border-gray-200 pb-4">
                <p className="text-gray-900 font-medium mb-4">
                  {index + 1}. {question.text}
                </p>

                {/* âœ… If is_ans = false â†’ show Get Answer button */}
                {question.is_ans === false ? (
                  <div>
                    <Button
                      variant="outline"
                      size="sm"
                      className="border-blue-600 text-blue-600 hover:bg-blue-50"
                      onClick={() => handleGetIDMCAnswer(question.id)}
                    >
                      Get Answer from IDMC
                    </Button>
                    {answers[question.id] && (
                      <p className="mt-2 text-sm text-green-600">
                        {answers[question.id]}
                      </p>
                    )}
                  </div>
                ) : (
                  /* âœ… Else show Yes/No options */
                  <div className="flex items-center gap-6 mb-4">
                    <label className="flex items-center gap-2 cursor-pointer">
                      <input
                        type="radio"
                        name={`question-${question.id}`}
                        value="yes"
                        checked={answers[question.id] === "yes"}
                        onChange={(e) => handleAnswerChange(question.id, e.target.value)}
                        className="w-4 h-4 text-primary"
                      />
                      <span className="text-sm text-gray-700">Yes</span>
                    </label>
                    <label className="flex items-center gap-2 cursor-pointer">
                      <input
                        type="radio"
                        name={`question-${question.id}`}
                        value="no"
                        checked={answers[question.id] === "no"}
                        onChange={(e) => handleAnswerChange(question.id, e.target.value)}
                        className="w-4 h-4 text-primary"
                      />
                      <span className="text-sm text-gray-700">No</span>
                    </label>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </section>

      <Footer />
    </main>
  )
}
  